@import 'variables';
@import 'libs/normalize';

@import 'components/all';

body {
  background-color: $background;

  &.fullPizzaActive{
    overflow: hidden;
    padding-right: 10px;
  }
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;

  @media (max-width:415px) {
    width: calc(100vw - 20px);
  }
}

.content {
  padding: 40px 0;

  &__title {
    margin: 35px 0;
  }

  &__items__wrapper{
    display: flex;
    justify-content: center;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-column-gap: 35px;
    grid-row-gap: 0px;

    @media (max-width:1465px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width:1080px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width:755px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width:1170px) {
      flex-direction: column;
      row-gap: 20px;
      align-items: start;
    }
  }

  &__error{
    grid-column: 1/6;
    text-align: center;
    padding: 200px 0;

    p{
        font-size: 64px;
    }
    h3{
        font-size: 64px;
        @media(max-width: 850px){
          font-size: 38px;
        }
        @media(max-width: 400px){
          font-size: 30px;
        }
    }
    h4{
        font-size: 24px;
        color: #777777;
        @media(max-width: 850px){
          font-size: 16px;
        }
    }
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 820px;
    margin: 90px auto;
    .content__title {
      margin: 0;
    }
    @media (max-width: 440px){
      margin: 40px auto 90px auto;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    row-gap: 10px;

    @media (max-width: 500px) {
      flex-wrap: wrap;
    }
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;
    justify-content: space-between;
    column-gap: 15px;
    row-gap: 10px;

    @media (max-width: 1000px) {
      flex-wrap: wrap;
    }
    

    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;

      @media (max-width: 570px) {
        width: 100%;
        justify-content: center;
      }

      img {
        width: 80px;
        height: 80px;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex: 1 1 auto;

      @media (max-width: 570px) {
        flex: 1 1 100%;
        align-items: center;
        text-align: center;
      }

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex: 0 0 106px;

      @media (max-width: 1000px) {
        order: 1;
      }

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }

        &:disabled{
          border-color: darken($color: $gray-line, $amount: 10);

          svg {
            transform: none;
    
            path {
              fill: darken($color: $gray-line, $amount: 15);
            }
          }
        }
        &:hover:disabled{
          background-color: transparent;
        }
        &:active:disabled{
          transform: none;
        }
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 0 1 270px;

      @media (max-width: 1000px) {
        order: 1;
      }
      @media (max-width: 570px) {
        flex: 0 1 100px;
      }

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 4%;

      @media (max-width: 570px) {
        order: 1;
      }

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;
      row-gap: 10px;

      @media (max-width: 570px) {
        flex-wrap: wrap;
      }

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;
      row-gap: 15px;
      @media (max-width: 570px) {
        flex-wrap: wrap;
      }

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        padding: 16px;

        @media (max-width: 570px) {
          width: 100%;
        }

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;

        @media (max-width: 570px) {
          width: 100%;
        }
      }
    }
  }

  &--empty {
    margin: 0 auto;
    max-width: 560px;
    text-align: center;

    h2 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    .icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
      @media (max-width: 440px){
        width: 100%;
      }
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}




